/* harmony default export */ var paypage = ({
  name: "HelloWorld",
  data: function data() {
    return {
      msg: "Welcome to Your Vue.js App",
      shopData: [],
      busOrder: {},
      serOrLists: [],
      showDe: false,
      nowdate: '', //时间
      check: '', //页面支付方式是否选择
      andshow: false, //支付弹出支付方式
      closewx: false, //微信支付页面弹出
      closezfb: false //其他支付页面弹出
    };
  },
  created: function created() {
    var that = this;
    this.ajax.post("/xinda-api/business-order/detail", this.qs.stringify({
      businessNo: this.$route.query.id
    })).then(function (data) {
      var sData = data.data.data;
      that.shopData = sData;
      // console.log(that.shopData);
      that.busOrder = data.data.data.businessOrder;
      that.serOrLists = data.data.data.serviceOrderList;
    });
    //以下是杨迪补充的，时间部分
    var date = new Date();
    var Y = date.getFullYear() + '-';
    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
    var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
    var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
    var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
    var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
    // console.log(Y+M+D+h+m+s);
    this.nowdate = Y + M + D + h + m + s; //时间
  },

  methods: {
    paynow: function paynow() {
      if (!this.check) {
        //页面没有选择支付方式时
        this.andshow = true;
      } else {
        if (this.check == 'weixin') {
          this.andshow = false;
          this.closewx = true;
          this.closezfb = false;
          //console.log(this.check,'微信')
          this.ajax.post('xinda-api/pay/ weixin-pay', this.qs.stringify({
            businessNo: this.busOrder.businessNo
          })).then(function (data) {
            sessionStorage.setItem('orderdata', data.data);
            window.open('#/inner/orderData');
          });
        } else {
          this.andshow = false;
          this.closewx = false;
          this.closezfb = true;

          if (this.check == 'yinlian' | 'zizhu') {
            //银联
            //console.log(this.check,'银联',123)
            this.ajax.post('xinda-api/pay/china-pay', this.qs.stringify({
              businessNo: this.busOrder.businessNo
            })).then(function (data) {
              sessionStorage.setItem('orderdata', data.data);
              window.open('#/inner/orderData');
            });
          } else if (this.check == 'zhifubao') {
            //支付宝
            //console.log(this.check,'支付宝')
            this.ajax.post('xinda-api/pay/ali-pay', this.qs.stringify({
              businessNo: this.busOrder.businessNo
            })).then(function (data) {
              sessionStorage.setItem('orderdata', data.data);
              window.open('#/inner/orderData');
            });
          }
        }
      }
    },
    yinlianzf: function yinlianzf() {
      this.closezfb = true;
      this.check = 'yinlian';
      //console.log(this.check,'银联',123)
      this.ajax.post('xinda-api/pay/china-pay', this.qs.stringify({
        businessNo: this.busOrder.businessNo
      })).then(function (data) {
        sessionStorage.setItem('orderdata', data.data);
        console.log(data.data);
        window.open('#/inner/orderData');
      });
    },
    weixinzf: function weixinzf() {
      this.closewx = true;
      this.check = 'weixin';
      //console.log(this.check,'微信')
      this.ajax.post('xinda-api/pay/ weixin-pay', this.qs.stringify({
        businessNo: this.busOrder.businessNo
      })).then(function (data) {
        sessionStorage.setItem('orderdata', data.data);
        window.open('#/inner/orderData');
      });
    },
    zhifubaozf: function zhifubaozf() {
      this.closezfb = true;
      this.check = 'zhifubao';
      //console.log(this.check,'支付宝')
      this.ajax.post('xinda-api/pay/ali-pay', this.qs.stringify({
        businessNo: this.busOrder.businessNo
      })).then(function (data) {
        sessionStorage.setItem('orderdata', data.data);
        window.open('#/inner/orderData');
      });
    },
    alreadypay: function alreadypay() {
      //点击支付成功
      sessionStorage.setItem('busOrder', this.busOrder.businessNo);
    }
  }
});
